@media (min-width: 300px) and (max-width: 500px) {

  .filter-Btn {
    display: flex;
    justify-content: center;
  }

  .btn-filter {
    background-color: transparent;
    border: none;
    cursor: pointer;
    overflow: hidden;
    padding: 10px;
  }

  .recipes {
    border: 1px solid rgb(214, 214, 214);
    border-radius: 7px;
    display: flex;
    margin: 10px 19px 10px 23px;
    position: relative;
  }

  .details {
    padding-left: 10px;
    text-align: justify;
  }

  .name-Recipe {
    color: black;
    cursor: pointer;
    height: 700px;
    text-decoration: none;
  }

  .name {
    font-size: 12px;
    margin-top: 23px;
  }

  .other-details, .doneDate {
    font-size: 9px;
  }

  .other-details {
    color: gray;
    margin-top: 3px;
  }

  .doneDate {
    margin-bottom: 25px;
    margin-top: 25px;
  }

  .tag {
    background-color: rgb(209, 209, 209);
    border-radius: 10px;
    color: rgb(131, 131, 131);
    font-size: 14px;
    margin: auto;
    text-align: center;
    overflow: hidden;
    max-height: 30px;
    max-width: 80px;
  }

  #all {
    border: solid black 5px;
  }

  .tag-container {
    display: flex;
  }

  .image {
    border-bottom-left-radius: 7px;
    border-top-left-radius: 7px;
    margin-bottom: -4px;
    width: 140px;
  }

  .share-Btn {
    background-color: transparent;
    border: none;
    cursor: pointer;
    height: 30px;
    margin-top: 15px;
    overflow: hidden;
    width: 35px;
  }

  .details-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
  }

  .done-details-container {
    align-items: center;
    background: linear-gradient(-45deg, #f50043 0%, #d72756fd 100%);
    box-shadow: 0 2px 5px 0 rgba(2, 2, 2, 0.445);
    display: flex;
    height: 250px;
    justify-content: space-between;
    margin: 10px 0;
  }

  .nothing-recipe {
    background: linear-gradient(to bottom, #e4184e 5%, #a6143b 100%);
    border-radius: 10px;
    color: white;
    margin: auto;
    margin-top: 40px;
    text-align: center;
    padding: 10px;
    width: 80%;
  }

  .category-and-nacionality {
    text-align: center;
    padding: 0 5px 0 0;
  }

  .favorite-allDone {
    font-size: 18px;
    text-align: center;
    padding: 4px 0;
  }

  .favorite-cate {
    font-size: 16px;
    font-weight: 900;
  }

  .done-number {
    color: rgb(160, 158, 158);
    height: 20px;
    text-align: center;
    text-shadow: 0.5px 0.5px 0 rgba(128, 128, 128, 0.677);
    position: absolute;
    right: 8px;
    margin-top: -200px;
    width: 20px;
  }
}

